	checkTabPermissions( tab ) {
		var retval = false;

		switch ( tab ) {
			case 'tab_task_amendments':
				if ( PermissionManager.validate( 'job_item', 'edit' ) || PermissionManager.validate( 'job_item', 'edit_child' ) || PermissionManager.validate( 'job_item', 'edit_own' ) ) {
					retval = true;
				}
				break;
			default:
				retval = super.checkTabPermissions( tab );
				break;
		}

		return retval;
	}

	initSubJobItemAmendmentView() {
		var $this = this;

		if ( !this.current_edit_record.id ) {
			TTPromise.resolve( 'BaseViewController', 'onTabShow' ); //Since search() isn't called in this case, and we just display the "Please Save This Record ..." message, resolve the promise.
			return;
		}

		if ( this.sub_job_item_amendment_view_controller ) {
			this.sub_job_item_amendment_view_controller.buildContextMenu( true );
			this.sub_job_item_amendment_view_controller.setDefaultMenu();
			$this.sub_job_item_amendment_view_controller.parent_key = 'job_id';
			$this.sub_job_item_amendment_view_controller.parent_value = $this.current_edit_record.id;
			$this.sub_job_item_amendment_view_controller.parent_edit_record = $this.current_edit_record;
			$this.sub_job_item_amendment_view_controller.parent_view_controller = $this;
			$this.sub_job_item_amendment_view_controller.initData();
			return;
		}

		Global.loadScript( 'views/attendance/job_item_amendment/JobItemAmendmentViewController.js', function() {
			var tab_employee_criteria = $this.edit_view_tab.find( '#tab_task_amendments' );
			var firstColumn = tab_employee_criteria.find( '.first-column-sub-view' );
			Global.trackView( 'Sub' + 'JobItemAmendment' + 'View' );
			JobItemAmendmentViewController.loadSubView( firstColumn, beforeLoadView, afterLoadView );

		} );

		function beforeLoadView() {

		}

		function afterLoadView( subViewController ) {
			$this.sub_job_item_amendment_view_controller = subViewController;
			$this.sub_job_item_amendment_view_controller.parent_key = 'job_id';
			$this.sub_job_item_amendment_view_controller.parent_value = $this.current_edit_record.id;
			$this.sub_job_item_amendment_view_controller.parent_edit_record = $this.current_edit_record;
			$this.sub_job_item_amendment_view_controller.parent_view_controller = $this;
			$this.sub_job_item_amendment_view_controller.initData();
		}
	}

	setCurrentEditRecordData() {

		// When mass editing, these fields may not be the common data, so their value will be undefined, so this will cause their change event cannot work properly.
		this.setDefaultData( {
			'user_branch_selection_type_id': 10,
			'user_department_selection_type_id': 10,
			'user_group_selection_type_id': 10,
			'job_item_group_selection_type_id': 10
		} );
		for ( var key in this.current_edit_record ) {
			var widget = this.edit_view_ui_dic[key];
			if ( Global.isSet( widget ) ) {
				switch ( key ) {
					case 'country': //popular case
						if ( !this.current_edit_record[key] ) {
							this.current_edit_record[key] = LocalCacheData.getCurrentCompany().country;
						}
						this.eSetProvince( this.current_edit_record[key] );
						widget.setValue( this.current_edit_record[key] );
						break;
					case 'province':
						if ( !this.current_edit_record[key] ) {
							this.current_edit_record[key] = LocalCacheData.getCurrentCompany().province;
						}
						widget.setValue( this.current_edit_record[key] );
						break;
					default:
						widget.setValue( this.current_edit_record[key] );
						break;
				}
			}
		}

		this.collectUIDataToCurrentEditRecord();
		this.setEditViewDataDone();
	}

	setEditViewDataDone() {
		super.setEditViewDataDone();
		this.onEmployeeGroupSelectionTypeChange();
		this.onEmployeeTitleSelectionTypeChange();
		this.onBranchSelectionTypeChange();
		this.onDepartmentSelectionTypeChange();
		this.isDisableIncludeEmployees();
		this.onJobItemGroupSelectionTypeChange();
		this.isDisableIncludeTasks();
	}

	onFormItemChange( target, doNotValidate ) {
		this.setIsChanged( target );
		this.setMassEditingFieldsWhenFormChange( target );
		var key = target.getField();
		var c_value = target.getValue();

		switch ( key ) {
			case 'country':
				var widget = this.edit_view_ui_dic['province'];
				widget.setValue( null );
				break;
		}

		this.current_edit_record[key] = c_value;

		if ( key === 'country' ) {
			this.onCountryChange();
		}
		if ( key === 'user_group_selection_type_id' ) {
			this.onEmployeeGroupSelectionTypeChange();
		}
		if ( key === 'user_title_selection_type_id' ) {
			this.onEmployeeTitleSelectionTypeChange();
		}
		if ( key === 'user_branch_selection_type_id' ) {
			this.onBranchSelectionTypeChange();
		}
		if ( key === 'user_department_selection_type_id' ) {
			this.onDepartmentSelectionTypeChange();
		}
		if ( key === 'job_item_group_selection_type_id' ) {
			this.onJobItemGroupSelectionTypeChange();
		}
		this.isDisableIncludeEmployees();
		this.isDisableIncludeTasks();
		if ( !doNotValidate ) {
			this.validate();
		}
	}

	eSetProvince( val, refresh ) {
		var $this = this;
		var province_widget = $this.edit_view_ui_dic['province'];

		if ( !val || val === '-1' || val === '0' ) {
			$this.e_province_array = [];
			province_widget.setSourceData( [] );
		} else {
			this.company_api.getOptions( 'province', val, {
				onResult: function( res ) {
					res = res.getResult();
					if ( !res ) {
						res = [];
					}
					$this.e_province_array = Global.buildRecordArray( res );
					if ( refresh && $this.e_province_array.length > 0 ) {
						$this.current_edit_record.province = $this.e_province_array[0].value;
						province_widget.setValue( $this.current_edit_record.province );
					}
					province_widget.setSourceData( $this.e_province_array );

				}
			} );
		}
	}

	isDisableIncludeEmployees() {
		if ( this.edit_view_ui_dic['user_group_ids'].getEnabled() || this.edit_view_ui_dic['user_branch_ids'].getEnabled() || this.edit_view_ui_dic['user_department_ids'].getEnabled() ) {
			this.edit_view_ui_dic['include_user_ids'].setEnabled( true );
		} else {
			this.edit_view_ui_dic['include_user_ids'].setEnabled( false );
		}
	}

	isDisableIncludeTasks() {
		if ( this.edit_view_ui_dic['job_item_group_ids'].getEnabled() ) {
			this.edit_view_ui_dic['include_job_item_ids'].setEnabled( true );
		} else {
			this.edit_view_ui_dic['include_job_item_ids'].setEnabled( false );
		}
	}

	onEmployeeGroupSelectionTypeChange() {
		if ( this.current_edit_record['user_group_selection_type_id'] == 10 ) {
			this.edit_view_ui_dic['user_group_ids'].setEnabled( false );
		} else {
			this.edit_view_ui_dic['user_group_ids'].setEnabled( true );
		}
	}

